import React from 'react';
import { ConfigProvider } from 'antd';
import styles from './Tasks.module.css';
import { TonConnectUI } from '@tonconnect/ui';
import { Button } from 'antd';
import { SendOutlined,  LoadingOutlined } from '@ant-design/icons';

const Tasks = () => (
  <ConfigProvider
    theme={{
      token: {
        // Seed Token
        colorPrimary: '#black',
        borderRadius: 10,
      },
    }}
  >
    <div className={styles.wallet}>
      <h1 className={styles.beta}>Available Tasks:</h1>
      <h1 className={styles.beta2}>Tasks will be shown soon</h1>
      <h1 className={styles.beta3}>Waiting for tasks... <br /><LoadingOutlined /></h1>
      <h1 className={styles.beta1}>But for now, join our channel, fren:</h1>
      <a target='_blank' href="https://t.me/notclicker_scam"><Button type='primary' icon={<SendOutlined />} className={styles.button} size={'large'}>Not Clicker Community</Button></a>
      {/* Add your wallet page content here */}
    </div>
  </ConfigProvider>
);

export default Tasks;