import { ReactNode, useRef, useState, useEffect  } from 'react'
import { AnimatePresence, AnimateSharedLayout, motion } from 'framer-motion'
import { WarningOutlined } from '@ant-design/icons';
import cn from 'classnames';
import { Vibration } from 'react-native';
import { Progress } from 'antd';
import './Notcoin.css';

interface NotcoinProps {
  canIClickPlease: boolean
  sleep: boolean
  funMode: boolean
  clickValue: number
  cooldown: number
  handleClick(): void
  children?: ReactNode
}

interface NumberInfo {
  id: string
  value: number
  x: number
  y: number
}

const numberAnimationDurationMs = 1000
const numberAnimationDurationSec = numberAnimationDurationMs / 1000

const notCoinAppearence = {
  initial: {
    opacity: 0,
    scale: 0,
  },
  animate: {
    opacity: 1,
    scale: 1,
  },
  exit: {
    opacity: 0,
    scale: 0,
  },
  transition: {
    duration: 0.3,
    ease: [0, 0.71, 0.2, 1.01],
    scale: {
      type: 'spring',
      damping: 10,
      stiffness: 100,
      restDelta: 0.001,
    },
  },
}

const cooldownAppearence = {
  initial: {
    opacity: 0,
    scale: 0.7,
  },
  animate: {
    opacity: 1,
    scale: 1,
  },
  exit: {
    opacity: 0,
    scale: 0.5,
  },
  transition: {
    duration: 0.7,
  },
}

export const Notcoin = ({
  canIClickPlease,
  sleep,
  funMode,
  clickValue,
  cooldown,
  handleClick,
  children
}: NotcoinProps): JSX.Element => {
  const notCoinRef = useRef<HTMLParagraphElement>(null)
  const [balance, setBalance] = useState(100);
  const [percent, setPercent] = useState(100);
  const [power, setPower] = useState(1000);
  const [buttonTransform, setButtonTransform] = useState({
    scale: 1,
    translateZ: 0,
    rotateX: 0,
    rotateY: 0,
  })
  const [numbers, setNumbers] = useState<NumberInfo[]>([])

  useEffect(() => {
    // Save data to local storage whenever it changes
    localStorage.setItem('balance', balance.toString());
    localStorage.setItem('percent', percent.toString());
  }, [balance, percent]);

  useEffect(() => {
    // Load data from local storage
    const savedBalance = localStorage.getItem('balance');
    const savedPercent = localStorage.getItem('percent');
  
    if (savedBalance) setBalance(Number(savedBalance));
    if (savedPercent) setPercent(Number(savedPercent));
  }, []);

  const handleTouchStart = (event: any) => {
    setPercent(prevPercent => Math.max(prevPercent - 1, 0));
    setPower(prevPower => Math.max(prevPower - 10, 0));
    Vibration.vibrate();
    handleClick()
    Vibration.vibrate([1], true);
    Vibration.vibrate(500);
    if (notCoinRef.current) {
      Vibration.vibrate([1], true);
      Vibration.vibrate(500);
      const touch = event.touches[0]
      const rect = notCoinRef.current.getBoundingClientRect()
      const centerX = rect.left + rect.width / 2
      const centerY = rect.top + rect.height / 2

      const offsetX = touch.clientX - centerX
      const offsetY = centerY - touch.clientY

      const rotateXValue = offsetY * 0.1
      const rotateYValue = offsetX * 0.1

      setButtonTransform({
        scale: 1,
        translateZ: -5,
        rotateX: rotateXValue,
        rotateY: rotateYValue,
      })

      const randomNumberBetweenTenAndMinusTen =
        Math.floor(Math.random() * 21) - 10

      const newNumber: NumberInfo = {
        id: `${Date.now()}`,
        value: clickValue * 10,
        x: touch.clientX + randomNumberBetweenTenAndMinusTen,
        y: touch.clientY
      }

      setNumbers((prevNumbers) => [...prevNumbers, newNumber])

      // Remove the number after the animation duration
      setTimeout(() => {
        setNumbers((prevNumbers) =>
          prevNumbers.filter((number) => number.id !== newNumber.id)
        )
      }, numberAnimationDurationMs)

      // Update balance
      setBalance(balance + clickValue * 10);
    }
  }

  const handleTouchEnd = () => {
    Vibration.vibrate();
    setButtonTransform({
      scale: 1,
      translateZ: 0,
      rotateX: 0,
      rotateY: 0,
    })
  }

  if (percent === 0) {
    setPercent(100);
  }

  if (power === 0) {
    setPower(1000);
  }
  return (
    <AnimatePresence mode="popLayout">
            <h1 className='beta'>App is in Beta Phase</h1>
            <h2 className="power">{power}/1000</h2>
            <h1 className="notbalance"><img src="https://cdn-0.emojis.wiki/emoji-pics/google/black-circle-google.png" alt="" style={{ width: '20px', height: '20px' }}/>  {balance}</h1>
            <Progress className='progressbar'percent={percent} percentPosition={{ align: 'center', type: 'inner' }} size={[380, 16]} showInfo={false} strokeColor="#E6F4FF"/>
      {canIClickPlease ? (
        <motion.div className="root" key="1" {...notCoinAppearence}>
          <div
            className={cn("container", {
              ["funMode"]: funMode,
              ["sleep"]: sleep,
            })}
          >
            {children}
            <div
              ref={notCoinRef}
              className={cn("notcoin", "skin-default", {
                ["sleep"]: sleep,
              })}
              onTouchStart={handleTouchStart}
              onTouchEnd={handleTouchEnd}
              style={{
                transform: `
                scale(${buttonTransform.scale})
                translateZ(${buttonTransform.translateZ}px)
                rotateX(${buttonTransform.rotateX}deg)
                rotateY(${buttonTransform.rotateY}deg)
              `,
              }}
            ></div>
          </div>
          <div>
            <AnimatePresence>
              {numbers.map((number) => {
                return (
                  <motion.div
                    key={number.id}
                    className="clickAmount"
                    initial={{ opacity: 1, y: number.y - 50, x: number.x }}
                    animate={{ opacity: 0, y: number.y - 200 }}
                    exit={{ opacity: 0 }}
                    transition={{ duration: numberAnimationDurationSec }}
                  >
                    {number.value}
                  </motion.div>
                )
              })}
            </AnimatePresence>
          </div>
        </motion.div>
      ) : (
        <motion.div className="root" key="2" {...cooldownAppearence}>
          <div className="cooldownContainer">
            <div className="cooldownNumber">
              {cooldown || <small>nothing</small>}
            </div>
            <svg className="cooldown">
              <circle
                className="cooldownCircle"
                r="140"
                cx="150"
                cy="150"
              ></circle>
            </svg>
          </div>
        </motion.div>
      )}
    </AnimatePresence>
  )
}